import { CSSProperties, useEffect, useRef } from "react";
import cls from "classnames";
import { BlockInstance, BlockPreviewInstance } from "@wptb/dev/types";
import { ListBlockProps, ListItemBlockProps } from "@wptb/dev/types";
import RichText, { RichTextRef } from "@wptb/ui/components/rich-text";
import { registerInnerBlock } from "@/block_registry";
import Control from "./control";
import { useBlockProps } from "@/public/hooks";
import { useBlock } from "@/public/hooks";
import { insertInnerBlock } from "@/public/block";

const useInfo = (props: ListItemBlockProps, blockPath: string[]) => {
  const { listIcon, itemSpacing, color, fontSize, type, alignment } =
    useBlockProps<ListBlockProps>(blockPath, -2);

  const pClass = type === "unordered" ? `wptb-list-style-type-${listIcon}` : "";

  const liStyle: CSSProperties = {
    marginBottom: itemSpacing,
  };

  const pStyle: CSSProperties = {
    color,
    fontSize: fontSize,
    justifyContent:
      {
        left: "flex-start",
        right: "flex-end",
      }[props.alignment || (alignment as string)] || "center",
  };

  let liClass = "wptb-in-element";
  const toolTip = props.toolTip?.trim();

  if (toolTip) {
    liClass += ` wptb-tooltip wptb-tooltip-${props.tooltipPosision}`;
  }

  return {
    pClass,
    liStyle,
    pStyle,
    liClass,
    toolTip,
  };
};

let shouldFocus: string | null = null;

const Edit = ({ name, index, blockPath }: BlockInstance) => {
  const editorRef = useRef<RichTextRef>();

  const { props, isActive, attributes, updateProps } =
    useBlock<ListItemBlockProps>({
      name,
      blockPath,
    });

  const split = (one: any, two: any) => {
    updateProps({ text: one });
    shouldFocus = insertInnerBlock(
      blockPath.at(-2)!,
      "list-item",
      { text: two },
      index! + 1,
      blockPath.slice(0, -1)
    );
  };

  const { pClass, liStyle, pStyle, liClass, toolTip } = useInfo(
    props,
    blockPath
  );

  useEffect(() => {
    if (shouldFocus === blockPath.at(-1)) {
      editorRef.current?.focus();
      shouldFocus = null;
    }
  }, [blockPath]);

  return (
    <li
      className={cls(liClass, {
        "wptb-active": isActive,
        "wptb-list-item-empty": (editorRef.current?.length() || 1) <= 1,
      })}
      style={liStyle}
      {...attributes}
    >
      <div className="wptb-list-item-content" style={{ position: "relative" }}>
        <RichText
          ref={editorRef as any}
          value={props.text}
          props={
            {
              "data-list-style-type-index": `${index! + 1}.`,
              className: pClass + " ql-container ql-snow",
              style: pStyle,
            } as any
          }
          onChange={(update) => updateProps({ text: update })}
          onAlignmentChange={(u) =>
            updateProps({
              alignment: !u || u === "justify" ? "left" : u,
            })
          }
          onSplit={split}
        />
      </div>
      {toolTip && !isActive && <div className="wptb-m-tooltip">{toolTip}</div>}
    </li>
  );
};

const Preview = ({ props, blockPath }: BlockPreviewInstance) => {
  const { pClass, liStyle, pStyle, liClass, toolTip } = useInfo(
    props,
    blockPath
  );

  return (
    <li className={liClass} style={liStyle}>
      <div className="wptb-list-item-content" style={{ position: "relative" }}>
        <p
          className={pClass}
          style={pStyle}
          dangerouslySetInnerHTML={{ __html: props.text }}
        />
      </div>
      {toolTip && <div className="wptb-m-tooltip">{toolTip}</div>}
    </li>
  );
};

registerInnerBlock<ListItemBlockProps>({
  name: "list-item",
  Edit,
  Preview,
  Control,
  defaultProps: {
    text: "List Item",
  },
  supportedParents: ["list"],
});
