import { ShortcodeIcon } from "@wptb/ui/icons/elements";
import { registerBlock } from "../../block_registry";

import Control from "./control";
import parseBlock from "./parse";
import { ShortcodeBlockProps } from "@wptb/dev/types";
import { BlockInstance, BlockPreviewInstance } from "@wptb/dev/types";
import { useBlock } from "@/public/hooks";
import { useMemo } from "react";
import { getSpacingStyle } from "@wptb/utils/style";

const Edit = ({ name, blockPath }: BlockInstance) => {
  const { props, attributes } = useBlock<ShortcodeBlockProps>({
    name,
    blockPath,
  });
  const style = useMemo(() => {
    return {
      ...getSpacingStyle(props.margin, "margin"),
      ...getSpacingStyle(props.padding, "padding"),
    };
  }, [props]);
  return (
    <div
      className="wptb-shortcode-container wptb-ph-element wptb-element-shortcode-404"
      {...attributes}
      style={style}
    >
      <div
        style={{ position: "relative" }}
        dangerouslySetInnerHTML={{ __html: props.shortcode }}
      />
    </div>
  );
};

const Preview = ({ props }: BlockPreviewInstance<ShortcodeBlockProps>) => {
  const style = useMemo(() => {
    return {
      ...getSpacingStyle(props.margin, "margin"),
      ...getSpacingStyle(props.padding, "padding"),
    };
  }, [props]);
  return (
    <div
      className="wptb-shortcode-container wptb-ph-element wptb-element-shortcode-404"
      style={style}
    >
      <div
        style={{ position: "relative" }}
        dangerouslySetInnerHTML={{ __html: props.shortcode }}
      />
    </div>
  );
};

registerBlock<ShortcodeBlockProps>({
  name: "shortcode",
  title: "Shortcode",
  icon: ShortcodeIcon,
  Edit,
  Preview,
  Control,
  isBlock: (el: HTMLElement) =>
    el.classList.contains("wptb-shortcode-container"),
  parse: parseBlock,
  defaultProps: {
    shortcode: "[Shortcode]",
  },
});
