import UpsellControl from "@/builder/components/upsell-control";
import {
  AlignmentControl,
  ColorControl,
  PanelBody,
  RangeControl,
  SizeControl,
  SpacingControl,
  SwitchControl,
} from "@wptb/ui/components";
import { BlockControlProps } from "@wptb/dev/types";
import { StarRatingBlockProps } from "@wptb/dev/types";

const Control = ({
  blockProps,
  setBlockProps,
}: BlockControlProps<StarRatingBlockProps>) => {
  return (
    <PanelBody initialOpen title="Star Rating" className="wptb-settings">
      <RangeControl
        label="Max Rating"
        value={blockProps.maxValue}
        onChange={(v) => setBlockProps({ maxValue: v })}
        allowOutOfRange
        min={0}
        max={10}
      />

      <RangeControl
        label="Rating Value"
        value={blockProps.value}
        onChange={(v) => setBlockProps({ value: v })}
        min={0}
        max={blockProps.maxValue}
        step={0.2}
      />

      <SizeControl
        label="Star Size"
        value={blockProps.starSize}
        onChange={(v) => setBlockProps({ starSize: v })}
        allowOutOfRange
      />

      <AlignmentControl
        label="Content Alignment"
        value={blockProps.alignment}
        onChange={(x) => setBlockProps({ alignment: x })}
      />
      <ColorControl
        label="Star Color"
        value={blockProps.starColor}
        onChange={(x) => setBlockProps({ starColor: x })}
      />
      <SwitchControl
        label="Show Rating"
        checked={blockProps.showRating!}
        onChange={(x: any) => setBlockProps({ showRating: x })}
      />
      {blockProps.showRating && (
        <>
          <SizeControl
            label="Font Size"
            value={blockProps.fontSize}
            onChange={(x: any) => setBlockProps({ fontSize: x })}
          />
          <ColorControl
            label="Text Color"
            value={blockProps.color}
            onChange={(x: any) => setBlockProps({ color: x })}
          />
        </>
      )}
      <UpsellControl name="star-rating.padding">
        <SpacingControl label="Padding" value={blockProps?.padding} />
      </UpsellControl>
      <UpsellControl name="star-rating.margin">
        <SpacingControl label="Margin" value={blockProps?.margin} />
      </UpsellControl>
    </PanelBody>
  );
};

export default Control;
