import cls from "classnames";
import { ListIcon } from "@wptb/ui/icons/elements";
import { registerBlock } from "@/block_registry";

import Control from "./control";
import parseBlock from "./parse";
import { BlockInstance, BlockPreviewInstance } from "@wptb/dev/types";
import { ListBlockProps, ListItemBlockProps } from "@wptb/dev/types";
import { getSpacingStyle } from "@wptb/utils/style";
import { useBlock } from "@/public/hooks";

import "./list-item";

const Edit = ({ name, blockPath, children }: BlockInstance) => {
  const { props, isActive, attributes } = useBlock<ListBlockProps>({
    name,
    blockPath,
  });
  const { padding, margin } = props;

  const listStyle = {
    ...getSpacingStyle(padding, "padding"),
    ...getSpacingStyle(margin, "margin"),
  };

  return (
    <div
      className={cls(
        "wptb-list-container wptb-ph-element wptb-element-list-404",
        {
          "wptb-list-active": isActive,
        }
      )}
      style={listStyle}
      {...attributes}
    >
      <ul>{children}</ul>
    </div>
  );
};

const Preview = ({ props, children }: BlockPreviewInstance<ListBlockProps>) => {
  const { padding, margin } = props;

  const listStyle = {
    ...getSpacingStyle(padding, "padding"),
    ...getSpacingStyle(margin, "margin"),
  };

  return (
    <div
      className="wptb-list-container wptb-ph-element wptb-element-list-404"
      style={listStyle}
    >
      <ul>{children}</ul>
    </div>
  );
};

registerBlock<ListBlockProps, ListItemBlockProps>({
  name: "list",
  title: "List",
  Edit,
  Preview,
  icon: ListIcon,
  Control,
  isBlock: (el: HTMLElement) => el.classList.contains("wptb-list-container"),
  parse: parseBlock,
  defaultProps: {
    type: "ordered",
    alignment: "left",
  },
  defaultInnerBlocks: [
    ["list-item", { text: "List item 1" }],
    ["list-item", { text: "List item 2" }],
  ],
  supportedChildren: ["list-item"],
});
