import { useSelector } from "react-redux";
import Row from "./Row";
import { RootState } from "../../store";
import { TableProps } from "@wptb/dev/types";
import { CSSProperties, useMemo } from "react";
import { TABLE_ID } from "@/store/table";
import {
  calcTableMinWidth,
  getTableAttrs,
  getTableStyle,
  getTBodyAttrs,
} from "@/builder/blocks/get-attrs";

interface TableRCProps {
  id: string;
}

const Table = ({ id }: TableRCProps) => {
  const table = useSelector((state: RootState) => {
    const device = state.editorSettings.devicePreview;
    return state.table[id]?.[device];
  });

  const props = useSelector((state: RootState) => {
    return state.table[id]?.blocksProps[TABLE_ID] as TableProps;
  });

  const { tblStyle, tbodyAttrs, tableAttrs, containerStyle } = useMemo(() => {
    if (!props) {
      return {
        tableAttrs: {},
        tblStyle: {},
        tbodyAttrs: {},
      };
    }
    const calced = calcTableMinWidth(props);
    const containerStyle: CSSProperties = {};
    if (!props?.alignment || props.alignment === "center") {
      containerStyle.marginLeft = "auto";
      containerStyle.marginRight = "auto";
    } else if (props.alignment === "right") {
      containerStyle.marginLeft = "auto";
    }

    if (props?.enableMaxWidth) {
      containerStyle.maxWidth = props?.maxWidth;
    }
    if (calced.total <= 700) {
      containerStyle.maxWidth = "700px";
    }

    return {
      tableAttrs: getTableAttrs(props, calced),
      tblStyle: getTableStyle(props, calced.minWidth),
      tbodyAttrs: getTBodyAttrs(props),
      containerStyle,
    };
  }, [props]);

  return !table ? null : (
    <div className="wptb-builder-table-container" style={containerStyle}>
      <table {...tableAttrs} style={tblStyle}>
        <tbody {...tbodyAttrs}>
          {table.blockOrder.map((rowId, index) => (
            <Row
              key={rowId}
              tableId={id}
              blockOrder={table.innerBlocks[rowId]}
              id={rowId}
              tableProps={props}
              rowIndex={index}
            />
          ))}
        </tbody>
      </table>
    </div>
  );
};

export default Table;
