import { CustomHtmlIcon } from "@wptb/ui/icons/elements";
import { registerBlock } from "../../block_registry";

import Control from "./control";
import parseBlock from "./parse";
import { CustomHtmlBlockProps } from "@wptb/dev/types";
import { BlockInstance, BlockPreviewInstance } from "@wptb/dev/types";
import { useBlock } from "@/public/hooks";
import { useMemo } from "react";
import { getSpacingStyle } from "@wptb/utils/style";

const Edit = ({ name, blockPath }: BlockInstance) => {
  const { props, attributes } = useBlock<CustomHtmlBlockProps>({
    name,
    blockPath,
  });

  const style = useMemo(() => {
    return {
      ...getSpacingStyle(props.margin, "margin"),
      ...getSpacingStyle(props.padding, "padding"),
    };
  }, [props]);

  return (
    <div
      className="wptb-custom_html-container wptb-ph-element wptb-element-custom_html-404"
      style={style}
      {...attributes}
    >
      <div
        className="wptb-custom-html-wrapper"
        data-wptb-new-element="1"
        style={{
          position: "relative",
        }}
        dangerouslySetInnerHTML={{ __html: props.html }}
      />
    </div>
  );
};
const Preview = ({ props }: BlockPreviewInstance<CustomHtmlBlockProps>) => {
  const style = useMemo(() => {
    return {
      ...getSpacingStyle(props.margin, "margin"),
      ...getSpacingStyle(props.padding, "padding"),
    };
  }, [props]);

  return (
    <div
      className="wptb-custom_html-container wptb-ph-element wptb-element-custom_html-404"
      style={style}
    >
      <div
        className="wptb-custom-html-wrapper"
        data-wptb-new-element="1"
        style={{
          position: "relative",
        }}
        dangerouslySetInnerHTML={{ __html: props.html }}
      />
    </div>
  );
};

registerBlock<CustomHtmlBlockProps>({
  name: "custom-html",
  title: "Custom Html",
  Edit,
  Preview,
  icon: CustomHtmlIcon,
  Control,
  isBlock: (el: HTMLElement) =>
    el.classList.contains("wptb-custom_html-container"),
  parse: parseBlock,
  defaultProps: {
    html: "<span>Insert Custom HTML</span>",
  },
});
