import { StarRatingIcon } from "@wptb/ui/icons/elements";
import { registerBlock } from "@/block_registry";

import Control from "./control";
import parseBlock from "./parse";
import { BlockInstance, BlockPreviewInstance } from "@wptb/dev/types";
import { StarRatingBlockProps } from "@wptb/dev/types";
import { getSpacingStyle } from "@wptb/utils/style";
import { CSSProperties, useEffect, useState } from "react";
import { Star } from "./star";
import { useBlock } from "@/public/hooks";
import classNames from "classnames";

const getInfo = (props: StarRatingBlockProps) => {
  const style: CSSProperties = {
    textAlign: (props.alignment as any) || "center",
    ...getSpacingStyle(props.margin, "margin"),
    ...getSpacingStyle(props.padding, "padding"),
  };

  const value = props.value || 0;

  const fontSize = props.fontSize || "15px";
  const ratingStyle = {
    fontSize,
    lineHeight: fontSize,
    height: fontSize,
    color: props.color || "",
  };

  const total = props.maxValue || 0;
  const ratingDisplay = props.showRating ? "block" : "none";

  const starColor = props.starColor || "#000000";
  const starSize = props.starSize || "20px";

  return {
    style,
    total,
    ratingDisplay,
    ratingStyle,
    starColor,
    starSize,
    value,
  };
};

const Edit = ({ name, blockPath }: BlockInstance) => {
  const { props, attributes, updateProps, isActive } =
    useBlock<StarRatingBlockProps>({
      name,
      blockPath,
    });

  const {
    style,
    total,
    ratingDisplay,
    ratingStyle,
    starColor,
    starSize,
    value,
  } = getInfo(props);

  const [highlighted, setHighlighted] = useState(value);

  useEffect(() => {
    setHighlighted(value);
  }, [value]);

  return (
    <div
      data-star-count={total}
      style={style}
      {...attributes}
      className={classNames(
        "wptb-star_rating-container wptb-ph-element wptb-element-star_rating-404",
        {
          "wptb-active": isActive,
        }
      )}
    >
      <div className="wptb-rating-stars-box">
        <ul className="wptb-rating-stars-list">
          {Array(total)
            .fill(0)
            .map((_, i) => (
              <li key={i}>
                <Star
                  id={blockPath.at(-1)!}
                  index={i}
                  displayColor={starColor}
                  size={starSize}
                  value={highlighted}
                  setHighlighted={setHighlighted}
                  setValue={(val) => updateProps({ value: val })}
                  reset={() => setHighlighted(value)}
                />
              </li>
            ))}
        </ul>
        <div
          className="wptb-number-rating-box"
          style={{ display: ratingDisplay }}
        >
          <div className="wptb-number-rating" style={ratingStyle}>
            {value}/{total}
          </div>
        </div>
      </div>
    </div>
  );
};

const Preview = ({
  props,
  blockPath,
}: BlockPreviewInstance<StarRatingBlockProps>) => {
  const {
    style,
    total,
    ratingDisplay,
    ratingStyle,
    starColor,
    starSize,
    value,
  } = getInfo(props);

  return (
    <div
      data-star-count={total}
      style={style}
      className="wptb-star_rating-container wptb-ph-element wptb-element-star_rating-404"
    >
      <div className="wptb-rating-stars-box">
        <ul className="wptb-rating-stars-list">
          {Array(total)
            .fill(0)
            .map((_, i) => (
              <li key={i}>
                <Star
                  id={blockPath.at(-1)!}
                  index={i}
                  displayColor={starColor}
                  size={starSize}
                  value={value}
                />
              </li>
            ))}
        </ul>
        <div
          className="wptb-number-rating-box"
          style={{ display: ratingDisplay }}
        >
          <div className="wptb-number-rating" style={ratingStyle}>
            {value}/{total}
          </div>
        </div>
      </div>
    </div>
  );
};

registerBlock<StarRatingBlockProps>({
  name: "star-rating",
  title: "Star Rating",
  Edit,
  Preview,
  icon: StarRatingIcon,
  Control,
  isBlock: (el: HTMLElement) =>
    el.classList.contains("wptb-star_rating-container"),
  parse: parseBlock,
  defaultProps: {
    value: 2.5,
    starSize: "20px",
    starColor: "rgb(219, 172, 0)",
    maxValue: 5,
    alignment: "center",
    showRating: false,
  },
});
