import { v4 as uuidv4 } from "uuid";
import { BlockParser } from "@wptb/dev/types";
import { ButtonBlockProps } from "@wptb/dev/types";
import { parseSpacing } from "@wptb/utils/parser";

const parseBlock: BlockParser = (el, allProps) => {
  const bWrap: HTMLDivElement | null = el.querySelector(".wptb-button-wrapper");
  const lTarget: HTMLAnchorElement | null =
    el.querySelector(".wptb-link-target");
  const btnDiv: HTMLDivElement | null = el.querySelector(".wptb-button");
  const iconEl: HTMLDivElement | null = el.querySelector(".wptb-button-icon");
  const labelEl: HTMLDivElement | null = el.querySelector(".wptb-button-label");

  const pEl = el.querySelector("p");

  const props: ButtonBlockProps = {
    text: pEl?.innerHTML || "",
    fontSize: pEl?.style.fontSize,
    size: (bWrap?.className.match(/wptb-size-(s|m|l|xl)/)?.[1] as any) || "m",
    fullWidth: lTarget?.style.width === "100%",
    borderRadius: btnDiv?.style.borderRadius,
    background: btnDiv?.dataset.wptbElementBgColor,
    color: btnDiv?.dataset.wptbElementColor,
    buttonAlignment: bWrap?.style.justifyContent,
    contentAlignment: btnDiv?.style.justifyContent,
    id: lTarget?.id,
    // Url
    url: lTarget?.href,
    linkRel: lTarget?.getAttribute("rel")?.split(" "),
    linkTarget: lTarget?.getAttribute("target") as any,
    convertToAbsolute:
      lTarget?.dataset.wptbLinkEnableConvertRelative === "true",

    // Hover
    hoverBg: btnDiv?.dataset.wptbElementHoverBgColor,
    hoverColor: btnDiv?.dataset.wptbElementHoverTextColor,
    hoverScale: parseFloat(btnDiv?.dataset.wptbElementHoverScale || "1.0") || 1,

    // Icon
    icon: iconEl?.dataset.wptbButtonIconSrc || "",
    iconPosition: btnDiv?.classList.contains("wptb-plugin-button-order-right")
      ? "right"
      : "left",
    iconSize: iconEl?.style.height,

    // Label
    hasLabel: bWrap?.classList.contains("wptb-button-has-label"),
    labelBg: labelEl?.style.backgroundColor,
    labelColor: labelEl?.style.color,
    labelText: labelEl?.querySelector(".wptb-button-label-text")?.innerHTML,

    // Spacing
    padding: parseSpacing("padding", el.style),
    margin: parseSpacing("margin", el.style),
  };

  const bid = uuidv4();
  allProps[bid] = props;

  return [
    bid,
    {
      name: "button",
      blockOrder: [],
    },
  ];
};

export default parseBlock;
