import cls from "classnames";
import { createElement, CSSProperties, useMemo, useState } from "react";
import { ButtonIcon } from "@wptb/ui/icons/elements";
import { getBorderRadiusCss, getSpacingStyle } from "@wptb/utils/style";
import { registerBlock } from "@/block_registry";

import Control from "./control";
import parseBlock from "./parse";
import { BlockInstance, BlockPreviewInstance } from "@wptb/dev/types";
import { ButtonBlockProps } from "@wptb/dev/types";
import RichText from "@wptb/ui/components/rich-text";
import { useBlock } from "@/public/hooks";
import { NetIcon } from "@wptb/ui/components";

const getAttrs = (props: ButtonBlockProps, isHovered: boolean) => {
  const mainStyle = {
    ...getSpacingStyle(props.padding, "padding"),
    ...getSpacingStyle(props.margin, "margin"),
  };
  const btnStyle: CSSProperties = {
    borderRadius: getBorderRadiusCss(props.borderRadius),
    background: isHovered
      ? props.hoverBg || props.background
      : props.background,
    color: isHovered ? props.hoverColor || props.color : props.color,
    justifyContent: props.contentAlignment,
    position: "relative",
    transform:
      isHovered && props.hoverScale ? `scale(${props.hoverScale})` : undefined,
  };

  const hoverAttrs = {
    "data-wptb-element-hover-bg-color": props.hoverBg,
    "data-wptb-element-hover-text-color": props.hoverColor,
    "data-wptb-element-hover-scale": props.hoverScale,
  };

  const btnAttrs = {
    "data-wptb-element-bg-color": props.background,
    "data-wptb-element-color": props.color,
    ...hoverAttrs,
  };

  const labelStyle = {
    display: props.hasLabel ? "inline-flex" : "none",
    background: props.labelBg,
    color: props.labelColor,
  };
  const btnPStyle = {
    fontSize: props.fontSize ?? "15px",
  };

  return {
    mainStyle,
    btnStyle,
    btnAttrs,
    btnPStyle,
    labelStyle,
    hoverAttrs,
  };
};

const getLinkEl = (props: ButtonBlockProps) => {
  let lTag = "div";
  let lAttrs: any = {
    id: props.id,
    style: {
      width: props.fullWidth ? "100%" : null,
    },
  };

  if (props.url && props.url !== "") {
    lTag = "a";
    let url = props.url;
    if (props.convertToAbsolute && !/^https?:\/\//.test(props.url)) {
      url = "https://" + url.replace(/^\//, "");
    }
    lAttrs = {
      ...lAttrs,
      href: url,
      target: props.linkTarget,
      rel: props.linkRel?.join?.(" "),
      "data-wptb-link-enable-convert-relative": props.convertToAbsolute,
      onClick: (evt: any) => evt.preventDefault(),
    };
  }

  return { lTag, lAttrs };
};

const Edit = ({ name, blockPath }: BlockInstance) => {
  const [isLabelFocused, setLabelFocused] = useState(false);
  const [isHovered, setHovered] = useState(false);
  const { props, isActive, attributes, updateProps } =
    useBlock<ButtonBlockProps>({
      name,
      blockPath,
      onMouseEnter: () => setHovered(true),
      onMouseLeave: () => setHovered(false),
    });

  const { lTag, lAttrs } = useMemo(() => getLinkEl(props), [props]);
  const { mainStyle, btnStyle, btnAttrs, labelStyle, hoverAttrs, btnPStyle } =
    useMemo(() => getAttrs(props, isHovered), [props, isHovered]);

  let wrapperClass = `wptb-size-${props.size}`;
  if (props.hasLabel) {
    wrapperClass += " wptb-button-has-label";
  }
  const iconSize = props.iconSize ?? "25px";

  return (
    <div
      style={mainStyle}
      {...attributes}
      className={cls(
        "wptb-button-container wptb-ph-element wptb-element-button-404",
        {
          "wptb-label-focused": isLabelFocused,
          "wptb-active": isActive,
        }
      )}
    >
      <div
        className={`wptb-button-wrapper ${wrapperClass}`}
        style={{ justifyContent: props.buttonAlignment }}
      >
        {createElement(
          lTag,
          {
            className: "wptb-link-target",
            ...lAttrs,
          },
          <div
            className={`wptb-button wptb-plugin-button-order-${props.iconPosition ?? "left"}`}
            style={btnStyle}
            {...btnAttrs}
          >
            <RichText
              preventNewLine
              value={props.text}
              onChange={(text) => updateProps({ text })}
              toolbar={["bold", "italic", "underline", "strike"]}
              props={{ style: btnPStyle }}
              placeholder=""
            />
            <div
              className="wptb-button-icon"
              data-wptb-button-icon-src={props.icon || ""}
              style={{ width: iconSize, height: iconSize }}
              {...hoverAttrs}
            >
              <NetIcon name={props.icon as any} />
            </div>
            <div
              className="wptb-button-label"
              style={labelStyle}
              {...hoverAttrs}
            >
              <div
                className="wptb-button-label-decorator"
                style={{
                  borderColor: `rgba(0, 0, 0, 0) ${props.labelBg} rgba(0, 0, 0, 0)`,
                }}
              >
                <br />
              </div>
              <RichText
                preventNewLine
                value={props.labelText}
                onChange={(labelText) => updateProps({ labelText })}
                toolbar={["bold", "italic", "underline", "strike"]}
                props={{
                  onFocus: () => setLabelFocused(true),
                  onBlur: () => setLabelFocused(false),
                }}
                placeholder="Label"
              />
            </div>
          </div>
        )}
      </div>
    </div>
  );
};

const Preview = ({ props }: BlockPreviewInstance) => {
  const [isHovered, setHovered] = useState(false);

  const { lTag, lAttrs } = useMemo(() => getLinkEl(props), [props]);
  const { mainStyle, btnStyle, btnAttrs, labelStyle, hoverAttrs, btnPStyle } =
    useMemo(() => getAttrs(props, isHovered), [props, isHovered]);

  let wrapperClass = `wptb-size-${props.size}`;
  if (props.hasLabel) {
    wrapperClass += " wptb-button-has-label";
  }
  const iconSize = props.iconSize ?? "25px";

  return (
    <div
      style={mainStyle}
      className="wptb-button-container wptb-ph-element wptb-element-button-404"
      onMouseEnter={() => setHovered(true)}
      onMouseLeave={() => setHovered(false)}
    >
      <div
        className={`wptb-button-wrapper ${wrapperClass}`}
        style={{ justifyContent: props.buttonAlignment }}
      >
        {createElement(
          lTag,
          {
            className: "wptb-link-target",
            ...lAttrs,
          },
          <div
            className={`wptb-button wptb-plugin-button-order-${props.iconPosition ?? "left"}`}
            style={btnStyle}
            {...btnAttrs}
          >
            <p
              style={btnPStyle}
              dangerouslySetInnerHTML={{ __html: props.text }}
            />
            <div
              className="wptb-button-icon"
              data-wptb-button-icon-src={props.icon}
              style={{ width: iconSize, height: iconSize }}
              {...hoverAttrs}
            >
              <NetIcon name={props.icon as any} />
            </div>
            <div
              className="wptb-button-label"
              style={labelStyle}
              {...hoverAttrs}
            >
              <div
                className="wptb-button-label-decorator"
                style={{
                  borderColor: `rgba(0, 0, 0, 0) ${props.labelBg} rgba(0, 0, 0, 0)`,
                }}
              >
                <br />
              </div>
              <div
                {...hoverAttrs}
                className="wptb-button-label-text"
                dangerouslySetInnerHTML={{ __html: props.labelText }}
              />
            </div>
          </div>
        )}
      </div>
    </div>
  );
};

registerBlock<ButtonBlockProps>({
  name: "button",
  title: "Button",
  Edit,
  Preview,
  icon: ButtonIcon,
  Control,
  isBlock: (el: HTMLElement) => el.classList.contains("wptb-button-container"),
  parse: parseBlock,
  defaultProps: {
    text: "Button Text",
    size: "m",
    fullWidth: false,
    borderRadius: "5px",
    background: "rgb(50, 57, 63)",
    color: "#ffffff",
    buttonAlignment: "center",
    contentAlignment: "center",
    hoverScale: 1,
  },
});
