import React from "react";
import * as ReactRedux from "react-redux";
import ReactDOMClient from "react-dom/client";
import {
  registerBlock,
  registerEnhancement,
  registerInnerBlock,
} from "./block_registry";
import store from "./store";
import { TABLE_ID, tableReducer } from "./store/table";
import BuilderApp from "./builder";
import Table from "./public/table-preview";
import EnhancedControl from "./builder/components/enhanced-control";
import { insertInnerBlock, removeBlock } from "./public/block";
import {
  default as notify,
  Notifications,
  updateNotification,
} from "./public/notify";
import { useBlock, useBlockProps } from "./public/hooks";
import parseTable from "./legacy/parser";
import { editorReducer } from "./store/editor";
import * as components from "@wptb/ui/components";

import { ToolbarBtn } from "./builder/control-panel/Toolbar";
import TimeTravel from "./store/time-travel";
import { cloneBlock } from "./store/table/utils";
import { createNewTable } from "./public/utils";

import "./blocks";
import "./upsells";

const WPTB = {
  TABLE_ID,
  modules: {
    React: React,
    ReactRedux: ReactRedux,
    ReactDOMClient: ReactDOMClient,
    components,
  },
  store: {
    store,
    table: tableReducer,
    editor: editorReducer,
  },
  hooks: {
    useBlock,
    useBlockProps,
  },
  components: {
    Table,
    BuilderApp,
    Notifications,
    EnhancedControl,
    ToolbarBtn,
  },
  registry: {
    registerBlock,
    registerInnerBlock,
    registerEnhancement,
  },
  utils: {
    notify,
    updateNotification,
    parseTable,
    createNewTable,
    cloneBlock,
    insertInnerBlock,
    removeBlock,
    TimeTravel,
  },
} as const;

window.WPTB = WPTB;

export type WPTB = typeof WPTB;
