interface ChoiceGroupItem {
  label: string;
  value: string;
}

interface ChoiceGroupProps {
  label?: string;
  value?: string[];
  onChange?: (value: string[]) => void;
  items: ChoiceGroupItem[];
}

const ChoiceGroup = ({ label, value, onChange, items }: ChoiceGroupProps) => {
  return (
    <div className="wptb-choice-group wptb-control">
      {label && <p className="wptb-choice-group-label">{label}</p>}
      <div className="wptb-choice-group-items">
        {items.map((item) => {
          const checked = value?.includes(item.value);
          return (
            <label key={item.value}>
              <div className="wptb-checkbox">
                <input
                  type="checkbox"
                  key={item.value}
                  value={item.value}
                  checked={checked}
                  onChange={() =>
                    onChange?.(
                      checked
                        ? value?.filter((x) => x !== item.value)
                        : [...(value || []), item.value]
                    )
                  }
                />
                <span />
              </div>
              <p>{item.label}</p>
            </label>
          );
        })}
      </div>
    </div>
  );
};

export default ChoiceGroup;
