import { useState } from "react";
import { BlockControlProps } from "@wptb/dev/types";
import { ButtonBlockProps } from "@wptb/dev/types";
import {
  AlignCenterIcon,
  AlignLeftIcon,
  AlignRightIcon,
} from "@wptb/ui/icons/alignment";
import UpsellControl from "@/builder/components/upsell-control";
import {
  BorderRadiusControl,
  ChoiceGroup,
  ColorControl,
  IconPicker,
  PanelBody,
  RadioGroup,
  RangeControl,
  SelectControl,
  SizeControl,
  SpacingControl,
  SwitchControl,
  Tab,
  TextControl,
} from "@wptb/ui/components";

interface TabAttrs {
  blockProps: ButtonBlockProps;
  setBlockProps: (props: Partial<ButtonBlockProps>) => void;
}

const GeneralTab = ({ blockProps, setBlockProps }: TabAttrs) => {
  return (
    <div className="wptb-control-group">
      <RadioGroup
        label="Button Size"
        value={blockProps?.size}
        onChange={(size: any) => setBlockProps({ size })}
        items={[
          { label: "Small", value: "s", icon: "S" },
          { label: "Medium", value: "m", icon: "M" },
          { label: "Large", value: "l", icon: "L" },
          { label: "Extra Large", value: "xl", icon: "XL" },
        ]}
      />
      <SwitchControl
        label="Full Width"
        checked={blockProps?.fullWidth as any}
        onChange={(fullWidth: any) => setBlockProps({ fullWidth })}
      />
      <BorderRadiusControl
        label="Border Radius"
        value={blockProps?.borderRadius}
        onChange={(v) => setBlockProps({ borderRadius: v })}
      />
      <ColorControl
        label="Button Color"
        value={blockProps?.background}
        onChange={(background: any) => setBlockProps({ background })}
      />
      <ColorControl
        label="Button Hover Color"
        value={blockProps?.hoverBg}
        onReset={() => setBlockProps({ hoverBg: undefined })}
        onChange={(hoverBg: any) => setBlockProps({ hoverBg })}
      />
      <ColorControl
        label="Text Color"
        value={blockProps?.color}
        onChange={(color: any) => setBlockProps({ color })}
      />
      <ColorControl
        label="Text Hover Color"
        value={blockProps?.hoverColor}
        onReset={() => setBlockProps({ hoverColor: undefined })}
        onChange={(hoverColor: any) => setBlockProps({ hoverColor })}
      />
      <SizeControl
        label="Font Size"
        allowOutOfRangeMax
        value={blockProps?.fontSize}
        onReset={() => setBlockProps({ fontSize: undefined })}
        onChange={(fontSize: any) => setBlockProps({ fontSize })}
      />
      <RangeControl
        label="Scale on Hover"
        value={blockProps.hoverScale}
        onChange={(v) => setBlockProps({ hoverScale: v })}
        step={0.1}
        allowOutOfRange
        max={10}
      />
      <RadioGroup
        label="Button Alignment"
        value={blockProps.buttonAlignment}
        onChange={(x) => setBlockProps({ buttonAlignment: x })}
        items={[
          {
            label: "Align Left",
            value: "left",
            icon: AlignLeftIcon,
          },
          {
            label: "Align Center",
            value: "center",
            icon: AlignCenterIcon,
          },
          {
            label: "Align Right",
            value: "right",
            icon: AlignRightIcon,
          },
        ]}
      />
      <RadioGroup
        label="Content Alignment"
        value={blockProps.contentAlignment}
        onChange={(x) => setBlockProps({ contentAlignment: x })}
        items={[
          {
            label: "Align Left",
            value: "flex-start",
            icon: AlignLeftIcon,
          },
          {
            label: "Align Center",
            value: "center",
            icon: AlignCenterIcon,
          },
          {
            label: "Align Right",
            value: "flex-end",
            icon: AlignRightIcon,
          },
        ]}
      />
      <TextControl
        label="Button Id"
        value={blockProps?.id}
        onChange={(id) => setBlockProps({ id })}
        placeholder="Enter ID"
      />
      <UpsellControl name="button.padding">
        <SpacingControl label="Padding" value={blockProps?.padding} />
      </UpsellControl>
      <UpsellControl name="button.margin">
        <SpacingControl label="Margin" value={blockProps?.margin} />
      </UpsellControl>
    </div>
  );
};

const LinkTab = ({ blockProps, setBlockProps }: TabAttrs) => {
  return (
    <div className="wptb-control-group">
      <TextControl
        label="Button Link"
        value={blockProps.url}
        onChange={(url: any) => setBlockProps({ url })}
        placeholder="Insert Link Here"
      />
      <ChoiceGroup
        label="Link rel"
        items={[
          {
            label: "Sponsored",
            value: "sponsored",
          },
          {
            label: "No Follow",
            value: "nofollow",
          },
          {
            label: "No Referer",
            value: "noreferer",
          },
        ]}
        value={blockProps?.linkRel}
        onChange={(linkRel: any) => setBlockProps({ linkRel })}
      />
      <SelectControl
        label="Link Target"
        value={blockProps?.linkTarget}
        options={[
          {
            label: "Self",
            value: "_self",
          },
          {
            label: "Blank",
            value: "_blank",
          },
        ]}
        onChange={(linkTarget: any) => setBlockProps({ linkTarget })}
      />
      <SwitchControl
        label="Convert link to absolute"
        checked={blockProps.convertToAbsolute!}
        onChange={(x: any) => setBlockProps({ convertToAbsolute: x })}
      />
    </div>
  );
};

const IconTab = ({ blockProps, setBlockProps }: TabAttrs) => {
  return (
    <div className="wptb-control-group">
      <IconPicker
        label="Button Icon"
        value={blockProps.icon}
        onChange={(icon: any) => setBlockProps({ icon })}
        onReset={() => setBlockProps({ icon: undefined })}
      />
      <RadioGroup
        label="Icon Position"
        value={blockProps.iconPosition}
        onChange={(x: any) => setBlockProps({ iconPosition: x })}
        items={[
          {
            value: "left",
            icon: "Left",
          },
          {
            value: "right",
            icon: "Right",
          },
        ]}
      />
      <SizeControl
        allowOutOfRange
        label="Icon Size"
        value={blockProps?.iconSize}
        onReset={() => setBlockProps({ iconSize: undefined })}
        onChange={(iconSize: any) => setBlockProps({ iconSize })}
      />
    </div>
  );
};

const LabelTab = ({ blockProps }: TabAttrs) => {
  return (
    <div className="wptb-control-group">
      <UpsellControl name="button.label">
        <SwitchControl
          label="Enable Button Label"
          checked={blockProps.hasLabel!}
        />
        <ColorControl label="Label Color" value={blockProps?.labelBg} />
        <ColorControl label="Label Text Color" value={blockProps?.labelColor} />
      </UpsellControl>
    </div>
  );
};

const Control = ({
  blockProps,
  setBlockProps,
}: BlockControlProps<ButtonBlockProps>) => {
  const [activeTab, setActiveTab] = useState<string>("general");
  return (
    <PanelBody title="Button" initialOpen className="wptb-settings">
      <Tab
        active={activeTab}
        setActive={setActiveTab}
        tabs={{
          general: {
            title: "General",
            component: GeneralTab,
          },
          link: {
            title: "Link",
            component: LinkTab,
          },
          icon: {
            title: "Icon",
            component: IconTab,
          },
          label: {
            title: "Label",
            component: LabelTab,
          },
        }}
        props={{ blockProps, setBlockProps }}
      />
    </PanelBody>
  );
};

export default Control;
