import store from "@/store";
import { setActiveBlock } from "@/store/editor";
import { addBlock, removeBlock as internalRemoveBlock } from "@/store/table";
import TimeTravel from "@/store/time-travel";

export const insertInnerBlock = <
  T extends Record<string, any> = Record<string, any>,
>(
  parentId: string,
  name: string,
  props?: T,
  index?: number,
  setActive?: string[]
) => {
  const newId = {
    current: "",
  };
  TimeTravel.captured(addBlock, {
    parentId,
    blockName: name,
    props,
    index,
    newId,
  });
  if (setActive) {
    store.dispatch(setActiveBlock([...setActive, newId.current]));
  }
  return newId.current;
};

export const removeBlock = (id: string) => {
  TimeTravel.captured(internalRemoveBlock, {
    blockId: id,
  });
};
