import { v4 as uuidv4 } from "uuid";
import { BlockParser } from "@wptb/dev/types";
import { ImageBlockProps } from "@wptb/dev/types";
import { parseSpacing } from "@wptb/utils/parser";

const parseBlock: BlockParser = (el, allProps) => {
  const imgEl: HTMLImageElement | null = el.querySelector("img");
  const lTarget: HTMLAnchorElement | null =
    el.querySelector(".wptb-link-target");

  const props: ImageBlockProps = {
    src: imgEl?.src,
    alignment: el.dataset.wptbImageAlignment || "center",
    alt: imgEl?.getAttribute("alt") as any,
    imgHeight: parseFloat(imgEl?.getAttribute("height") || "1"),
    imgWidth: parseFloat(imgEl?.getAttribute("width") || "1"),

    sizeRelativeTo: el.dataset.wptbImageSizeRelative || "container",
    size: imgEl?.dataset.wptbSize,
    width: lTarget?.style.width || "100%",

    url: lTarget?.href,
    linkRel: lTarget?.getAttribute("rel")?.split(" ") || [],
    linkTarget: lTarget?.getAttribute("target") as any,
    convertToAbsolute:
      lTarget?.dataset.wptbLinkEnableConvertRelative === "true",

    padding: parseSpacing("padding", el.style),
    margin: parseSpacing("margin", el.style),
  };

  const bid = uuidv4();
  allProps[bid] = props;

  return [
    bid,
    {
      name: "image",
      blockOrder: [],
    },
  ];
};

export default parseBlock;
