import { useMemo } from "react";
import cls from "classnames";
import { BlockInstance, BlockPreviewInstance } from "@wptb/dev/types";
import { TextBlockProps } from "@wptb/dev/types";
import { TextIcon } from "@wptb/ui/icons/elements";
import RichText from "@wptb/ui/components/rich-text";
import { registerBlock } from "@/block_registry";
import Control from "./control";
import parseBlock from "./parse";
import { useBlock } from "@/public/hooks";
import { getSpacingStyle } from "@wptb/utils/style";

const getTextStyle = (props: TextBlockProps) => {
  return {
    color: props["color"],
    fontSize: props["fontSize"],
    "--wptb-link-color": props["linkColor"],
    ...getSpacingStyle(props.padding, "padding"),
    ...getSpacingStyle(props.margin, "margin"),
  };
};

const Edit = ({ name, blockPath }: BlockInstance) => {
  const { props, updateProps, isActive, attributes } = useBlock<TextBlockProps>(
    { name, blockPath }
  );
  const { style } = useMemo(() => {
    return {
      style: getTextStyle(props) as any,
    };
  }, [props]);

  return (
    <div
      className={cls(
        "wptb-text-container wptb-ph-element wptb-element-text-404",
        {
          "wptb-active": isActive,
        }
      )}
      style={style}
      {...attributes}
    >
      <RichText
        value={props.text}
        onChange={(value) => updateProps({ text: value })}
      />
    </div>
  );
};

const Preview = ({ props }: BlockPreviewInstance<TextBlockProps>) => {
  const { style } = useMemo(() => {
    return {
      style: getTextStyle(props) as any,
    };
  }, [props]);

  return (
    <div
      className="wptb-text-container wptb-ph-element wptb-element-text-404"
      style={style}
    >
      <div
        style={{ position: "relative" }}
        dangerouslySetInnerHTML={{ __html: props.text || "" }}
      />
    </div>
  );
};

registerBlock({
  name: "text",
  title: "Text",
  Edit,
  Preview,
  icon: TextIcon,
  Control,
  isBlock: (el: HTMLElement) => el.classList.contains("wptb-text-container"),
  parse: parseBlock,
  defaultProps: {
    text: "",
    fontSize: "1em",
    align: "left",
  },
});
