import { useCallback, useRef } from "react";

interface ImagePickerProps {
  label?: string;
  value?: string;
  onChange?: (url: string) => void;
}

const ImagePicker = ({ label, value, onChange }: ImagePickerProps) => {
  const imageFrameRef = useRef(null);

  const openMediaLibrary = useCallback(() => {
    let imageFrame = imageFrameRef.current;
    if (imageFrame) {
      imageFrame.open();
      return;
    }

    imageFrame = imageFrameRef.current = wp.media({
      title: "Select Image",
      button: {
        text: "Use This Image",
      },
      multiple: false,
    });

    imageFrame.on("open", () => {
      const modalElement = document.querySelector(
        ".media-modal"
      ) as HTMLDivElement;
      if (modalElement || !modalElement.dataset.isWptb) {
        modalElement.dataset.isWptb = "true";
        modalElement.addEventListener("pointerup", (event) => {
          event.stopPropagation();
        });
      }
    });

    imageFrame.on("select", function () {
      const attachment = imageFrame.state().get("selection").first().toJSON();
      onChange?.(attachment.url);
    });

    imageFrame.open();
  }, [onChange]);

  return (
    <div className="wptb-image-picker wptb-control">
      <div className="wptb-image-picker-left">
        {label && <p>{label}</p>}
        <button type="button" onClick={openMediaLibrary}>
          Select Image
        </button>
      </div>
      <button className="wptb-image-picker-right" onClick={openMediaLibrary}>
        {value && (
          <img
            src={value}
            alt="Selected"
            style={{ maxWidth: "300px", display: "block" }}
          />
        )}
      </button>
    </div>
  );
};

export default ImagePicker;
