import cls from "classnames";
import { ImageIcon } from "@wptb/ui/icons/elements";
import { registerBlock } from "@/block_registry";

import Control from "./control";
import parseBlock from "./parse";
import { BlockInstance, BlockPreviewInstance } from "@wptb/dev/types";
import { ImageBlockProps } from "@wptb/dev/types";
import { getSpacingStyle } from "@wptb/utils/style";
import { createElement, useMemo } from "react";
import { useBlock } from "@/public/hooks";

const getLinkEl = (props: ImageBlockProps) => {
  let lTag = "div";
  let lAttrs: any = {
    className: "wptb-link-target",
    style: {
      float: props.alignment === "center" ? "none" : props.alignment,
      width: props.width || "50%",
    },
  };

  if (props.url && props.url !== "") {
    lTag = "a";
    let url = props.url;
    if (props.convertToAbsolute && !/^https?:\/\//.test(props.url)) {
      url = "https://" + url.replace(/^\//, "");
    }
    lAttrs = {
      ...lAttrs,
      href: url,
      target: props.linkTarget,
      rel: props.linkRel?.join?.(" "),
      "data-wptb-link-enable-convert-relative": props.convertToAbsolute,
      onClick: (evt: any) => evt.preventDefault(),
    };
  }

  return { lTag, lAttrs };
};

const PlaceHolder = () => {
  return (
    <div className="wptb-image-placeholder">
      {ImageIcon}
      <p>Select Image</p>
    </div>
  );
};

const Edit = ({ name, blockPath }: BlockInstance) => {
  const { props, attributes, isActive } = useBlock<ImageBlockProps>({
    name,
    blockPath,
  });

  const wrapperAttrs = {
    "data-wptb-image-alignment": props.alignment,
    "data-wptb-image-size-relative": props.sizeRelativeTo,
  };

  const wrapperStyle = {
    ...getSpacingStyle(props.margin, "margin"),
    ...getSpacingStyle(props.padding, "padding"),
  };

  const imgAttrs = {
    "data-wptb-size": props.size,
    height: props.imgHeight,
    width: props.imgWidth,
    src: props.src,
    alt: props.alt,
  };

  const { lTag, lAttrs } = useMemo(() => getLinkEl(props), [props]);

  return (
    <div
      className={cls(
        "wptb-image-container wptb-ph-element wptb-element-image-404",
        {
          "wptb-active": isActive,
        }
      )}
      {...wrapperAttrs}
      style={wrapperStyle}
      {...attributes}
    >
      <div className="wptb-image-wrapper">
        {createElement(
          lTag,
          lAttrs,
          imgAttrs.src ? <img {...imgAttrs} /> : <PlaceHolder />
        )}
      </div>
    </div>
  );
};

const Preview = ({ props }: BlockPreviewInstance<ImageBlockProps>) => {
  const wrapperAttrs = {
    "data-wptb-image-alignment": props.alignment,
    "data-wptb-image-size-relative": props.sizeRelativeTo,
  };

  const wrapperStyle = {
    ...getSpacingStyle(props.margin, "margin"),
    ...getSpacingStyle(props.padding, "padding"),
  };

  const imgAttrs = {
    "data-wptb-size": props.size,
    height: props.imgHeight,
    width: props.imgWidth,
    src: props.src,
    alt: props.alt,
  };

  const { lTag, lAttrs } = useMemo(() => getLinkEl(props), [props]);

  return (
    <div
      className="wptb-image-container wptb-ph-element wptb-element-image-404"
      {...wrapperAttrs}
      style={wrapperStyle}
    >
      <div className="wptb-image-wrapper">
        {createElement(
          lTag,
          lAttrs,
          imgAttrs.src ? <img {...imgAttrs} /> : <PlaceHolder />
        )}
      </div>
    </div>
  );
};

registerBlock<ImageBlockProps>({
  name: "image",
  title: "Image",
  Edit,
  Preview,
  icon: ImageIcon,
  Control,
  isBlock: (el: HTMLElement) => el.classList.contains("wptb-image-container"),
  parse: parseBlock,
  defaultProps: {
    width: "100%",
    sizeRelativeTo: "container",
  },
});
