import { useState } from "react";
import { BlockControlProps } from "@wptb/dev/types";
import { ImageBlockProps } from "@wptb/dev/types";
import {
  AlignmentControl,
  ChoiceGroup,
  ImagePicker,
  PanelBody,
  SelectControl,
  SizeControl,
  SpacingControl,
  SwitchControl,
  Tab,
  TextControl,
} from "@wptb/ui/components";
import UpsellControl from "@/builder/components/upsell-control";

interface TabAttrs {
  blockProps: ImageBlockProps;
  setBlockProps: (props: Partial<ImageBlockProps>) => void;
}

const GeneralTab = ({ blockProps, setBlockProps }: TabAttrs) => {
  return (
    <div className="wptb-control-group">
      <ImagePicker
        label="Choose Image"
        value={blockProps.src}
        onChange={(src: any) => setBlockProps({ src })}
      />
      <AlignmentControl
        label="Image Alignment"
        value={blockProps.alignment}
        onChange={(alignment: any) => setBlockProps({ alignment })}
      />
      <TextControl
        label="Image Alt Text"
        value={blockProps.alt}
        onChange={(alt: any) => setBlockProps({ alt })}
        placeholder="Alt Text"
      />
      <UpsellControl name="image.padding">
        <SpacingControl label="Padding" value={blockProps?.padding} />
      </UpsellControl>
      <UpsellControl name="image.margin">
        <SpacingControl label="Margin" value={blockProps?.margin} />
      </UpsellControl>
    </div>
  );
};

const SizeTab = ({ blockProps, setBlockProps }: TabAttrs) => {
  return (
    <div className="wptb-control-group">
      <UpsellControl name="image.size_relative_to">
        <SelectControl
          label="Relative to"
          value={blockProps.sizeRelativeTo}
          options={[
            {
              label: "Container",
              value: "container",
            },
            {
              label: "Self",
              value: "self",
            },
          ]}
        />
      </UpsellControl>
      {WPTB_CFG.IS_PRO && blockProps.sizeRelativeTo === "self" ? (
        <UpsellControl name="image.size_self">{""}</UpsellControl>
      ) : (
        <SizeControl
          label="Size"
          value={blockProps?.width}
          onChange={(width: any) => setBlockProps({ width })}
          allowOutOfRangeMax
        />
      )}
    </div>
  );
};

const LinkTab = ({ blockProps, setBlockProps }: TabAttrs) => {
  return (
    <div className="wptb-control-group">
      <TextControl
        label="Button Link"
        value={blockProps.url}
        onChange={(url: any) => setBlockProps({ url })}
        placeholder="Insert Link Here"
      />
      <ChoiceGroup
        label="Link rel"
        items={[
          {
            label: "Sponsored",
            value: "sponsored",
          },
          {
            label: "No Follow",
            value: "nofollow",
          },
          {
            label: "No Referer",
            value: "noreferer",
          },
        ]}
        value={blockProps?.linkRel}
        onChange={(linkRel: any) => setBlockProps({ linkRel })}
      />
      <SelectControl
        label="Link Target"
        value={blockProps?.linkTarget}
        options={[
          {
            label: "Self",
            value: "_self",
          },
          {
            label: "Blank",
            value: "_blank",
          },
        ]}
        onChange={(linkTarget: any) => setBlockProps({ linkTarget })}
      />
      <SwitchControl
        label="Convert link to absolute"
        checked={blockProps.convertToAbsolute!}
        onChange={(x: any) => setBlockProps({ convertToAbsolute: x })}
      />
    </div>
  );
};

const Control = ({
  blockProps,
  setBlockProps,
}: BlockControlProps<ImageBlockProps>) => {
  const [activeTab, setActiveTab] = useState<string>("general");
  return (
    <PanelBody title="Image" initialOpen className="wptb-settings">
      <Tab
        active={activeTab}
        setActive={setActiveTab}
        props={{ blockProps, setBlockProps }}
        tabs={{
          general: { title: "General", component: GeneralTab },
          size: { title: "Size", component: SizeTab },
          link: { title: "Link", component: LinkTab },
        }}
      />
    </PanelBody>
  );
};

export default Control;
