import { BlockParser } from "@wptb/dev/types";
import { ShortcodeBlockProps } from "@wptb/dev/types";
import { parseSpacing } from "@wptb/utils/parser";
import { v4 as uuidv4 } from "uuid";

const parseBlock: BlockParser = (el, allBlocks) => {
  const cDiv = el.querySelector("div");

  const props: ShortcodeBlockProps = {
    shortcode: (cDiv?.querySelector("p") || cDiv)?.innerHTML || "",

    padding: parseSpacing("padding", el.style),
    margin: parseSpacing("margin", el.style),
  };
  const bid = uuidv4();
  allBlocks[bid] = props;

  return [
    bid,
    {
      name: "shortcode",
      innerBlocks: {},
      blockOrder: [],
    },
  ];
};

export default parseBlock;
