import { useMemo, useState } from "react";
import ControlLabel from "../control-label";

interface SizeXYControlProps {
  label?: string;
  value?: string;
  height?: number;
  width?: number;
  onChange?: (value: string) => void;
}

const SizeXYControl = ({
  height = 1,
  width = 1,
  label,
  value,
  onChange,
}: SizeXYControlProps) => {
  const [isLocked, setLocked] = useState(true);
  const [w, h, unit] = useMemo(() => {
    if (!value) {
      return [height, width, "px"];
    }
    const [w, h] = value.split(":");
    const unit = w?.replace(/[^a-z%]/gi, "") || "px";
    return [
      parseFloat(w.replace(/[^0-9.]/g, "1")),
      parseFloat(h.replace(/[^0-9.]/g, "1")),
      unit,
    ];
  }, [value, height, width]);

  const ratio = w / h;

  return (
    <div className="wptb-sizexy-control wptb-control">
      <ControlLabel
        label={label}
        onReset={() => onChange?.(`${height}px:${width}px`)}
      />
      <div className="wptb-sizexy-control-input">
        <label className="wptb-sizexy-control-input-item">
          <p>Width</p>
          <input
            type="number"
            min={1}
            value={w}
            onInput={(e: any) => {
              const w = +e.target.value || 1;
              const nh = isLocked ? (w / ratio).toFixed(2) : h;
              onChange?.(`${w}${unit}:${nh}${unit}`);
            }}
          />
        </label>
        <button
          type="button"
          className="wptb-sizexy-control-input-lock"
          onClick={() => setLocked(!isLocked)}
        >
          <svg
            xmlns="http://www.w3.org/2000/svg"
            viewBox="0 0 512 512"
            style={{
              fill: isLocked ? "red" : "green",
            }}
          >
            {isLocked ? (
              <path d="M304.083 405.907c4.686 4.686 4.686 12.284 0 16.971l-44.674 44.674c-59.263 59.262-155.693 59.266-214.961 0-59.264-59.265-59.264-155.696 0-214.96l44.675-44.675c4.686-4.686 12.284-4.686 16.971 0l39.598 39.598c4.686 4.686 4.686 12.284 0 16.971l-44.675 44.674c-28.072 28.073-28.072 73.75 0 101.823 28.072 28.072 73.75 28.073 101.824 0l44.674-44.674c4.686-4.686 12.284-4.686 16.971 0l39.597 39.598zm-56.568-260.216c4.686 4.686 12.284 4.686 16.971 0l44.674-44.674c28.072-28.075 73.75-28.073 101.824 0 28.072 28.073 28.072 73.75 0 101.823l-44.675 44.674c-4.686 4.686-4.686 12.284 0 16.971l39.598 39.598c4.686 4.686 12.284 4.686 16.971 0l44.675-44.675c59.265-59.265 59.265-155.695 0-214.96-59.266-59.264-155.695-59.264-214.961 0l-44.674 44.674c-4.686 4.686-4.686 12.284 0 16.971l39.597 39.598zm234.828 359.28l22.627-22.627c9.373-9.373 9.373-24.569 0-33.941L63.598 7.029c-9.373-9.373-24.569-9.373-33.941 0L7.029 29.657c-9.373 9.373-9.373 24.569 0 33.941l441.373 441.373c9.373 9.372 24.569 9.372 33.941 0z" />
            ) : (
              <path d="M326.612 185.391c59.747 59.809 58.927 155.698.36 214.59-.11.12-.24.25-.36.37l-67.2 67.2c-59.27 59.27-155.699 59.262-214.96 0-59.27-59.26-59.27-155.7 0-214.96l37.106-37.106c9.84-9.84 26.786-3.3 27.294 10.606.648 17.722 3.826 35.527 9.69 52.721 1.986 5.822.567 12.262-3.783 16.612l-13.087 13.087c-28.026 28.026-28.905 73.66-1.155 101.96 28.024 28.579 74.086 28.749 102.325.51l67.2-67.19c28.191-28.191 28.073-73.757 0-101.83-3.701-3.694-7.429-6.564-10.341-8.569a16.037 16.037 0 0 1-6.947-12.606c-.396-10.567 3.348-21.456 11.698-29.806l21.054-21.055c5.521-5.521 14.182-6.199 20.584-1.731a152.482 152.482 0 0 1 20.522 17.197zM467.547 44.449c-59.261-59.262-155.69-59.27-214.96 0l-67.2 67.2c-.12.12-.25.25-.36.37-58.566 58.892-59.387 154.781.36 214.59a152.454 152.454 0 0 0 20.521 17.196c6.402 4.468 15.064 3.789 20.584-1.731l21.054-21.055c8.35-8.35 12.094-19.239 11.698-29.806a16.037 16.037 0 0 0-6.947-12.606c-2.912-2.005-6.64-4.875-10.341-8.569-28.073-28.073-28.191-73.639 0-101.83l67.2-67.19c28.239-28.239 74.3-28.069 102.325.51 27.75 28.3 26.872 73.934-1.155 101.96l-13.087 13.087c-4.35 4.35-5.769 10.79-3.783 16.612 5.864 17.194 9.042 34.999 9.69 52.721.509 13.906 17.454 20.446 27.294 10.606l37.106-37.106c59.271-59.259 59.271-155.699.001-214.959z" />
            )}
          </svg>
        </button>
        <label className="wptb-sizexy-control-input-item">
          <p>Height</p>
          <input
            disabled={isLocked}
            type="number"
            min={1}
            value={h}
            onInput={(e: any) =>
              onChange?.(`${w}${unit}:${e.target.value}${unit}`)
            }
          />
        </label>
        <select
          value={unit}
          onChange={(e: any) =>
            onChange?.(`${w}${e.target.value}:${h}${e.target.value}`)
          }
        >
          <option value="px">px</option>
          <option value="%">%</option>
        </select>
      </div>
      <p className="wptb-sizexy-control-aspect">
        Aspect Ratio: <b>{ratio.toFixed(2)}</b>
      </p>
    </div>
  );
};

export default SizeXYControl;
