import {
  AlignCenterIcon,
  AlignLeftIcon,
  AlignRightIcon,
} from "../../icons/alignment";
import RadioGroup from "../radio-group";

interface AlignmentControlProps {
  label?: string;
  value?: string;
  onChange?: (value: string) => void;
}
const AlignmentControl = ({
  label = "Alignment",
  value,
  onChange,
}: AlignmentControlProps) => {
  return (
    <RadioGroup
      label={label}
      value={value}
      onChange={onChange}
      items={[
        {
          label: "Align Left",
          value: "left",
          icon: AlignLeftIcon,
        },
        {
          label: "Align Center",
          value: "center",
          icon: AlignCenterIcon,
        },
        {
          label: "Align Right",
          value: "right",
          icon: AlignRightIcon,
        },
      ]}
    />
  );
};

export default AlignmentControl;
