import { BlockParser } from "@wptb/dev/types";
import { StarRatingBlockProps } from "@wptb/dev/types";
import { parseSpacing } from "@wptb/utils/parser";

import { v4 as uuidv4 } from "uuid";

const parseBlock: BlockParser = (el, allBlocks) => {
  const firstStar = el.querySelector("li");
  const ratingBox: HTMLDivElement | null = el.querySelector(
    ".wptb-number-rating-box"
  );
  const ratingEl: HTMLDivElement | null = ratingBox?.firstElementChild as any;

  const props: StarRatingBlockProps = {
    starSize: firstStar?.style.height || "20px",
    starColor:
      firstStar?.querySelector("svg")?.style.fill || "rgb(219, 172, 0)",
    maxValue: parseInt(el.dataset.starCount || "5") || 5,
    alignment: el.style.textAlign,
    showRating: ratingBox?.style.display !== "none",
    fontSize: ratingEl?.style.fontSize,
    color: ratingEl?.style.color,

    value: Number(ratingEl?.textContent?.split("/")?.[0].trim() || 0),

    padding: parseSpacing("padding", el.style),
    margin: parseSpacing("margin", el.style),
  };
  const bid = uuidv4();
  allBlocks[bid] = props;

  return [
    bid,
    {
      name: "star-rating",
      blockOrder: [],
    },
  ];
};

export default parseBlock;
