interface TextControlProps {
  label?: string;
  value?: string;
  onChange?: (value: string) => void;
  placeholder?: string;
}

const TextControl = ({
  label,
  value,
  onChange,
  placeholder,
}: TextControlProps) => {
  return (
    <label className="wptb-text-control wptb-control">
      {label && <p className="wptb-text-control-label">{label}</p>}
      <input
        type="text"
        value={value || ""}
        onChange={(e) => onChange?.(e.target.value)}
        placeholder={placeholder}
      />
    </label>
  );
};

export default TextControl;
