import { useEffect, useRef } from "react";

interface StarProps {
  id: string;
  index: number;
  value: number;
  size: string;
  displayColor: string;
  setValue?: (value: number) => void;
  setHighlighted?: (value: number) => void;
  reset?: () => void;
}

export const Star = ({
  id,
  index,
  size,
  value,
  displayColor,
  setHighlighted,
  setValue,
  reset,
}: StarProps) => {
  const elRef = useRef<SVGSVGElement>(null);

  useEffect(() => {
    if (!elRef.current) {
      return;
    }

    const onMove = (evt: any) => {
      const rect = elRef.current?.getBoundingClientRect();
      if (!rect) {
        return;
      }
      const x = evt.clientX - rect.left;
      const val = index + x / rect.width;
      setHighlighted?.(val);
    };

    const onClick = (evt: any) => {
      const rect = elRef.current?.getBoundingClientRect();
      if (!rect) {
        return;
      }
      const x = evt.clientX - rect.left;
      const val = index + x / rect.width;
      setValue?.(val);
    };

    elRef.current.addEventListener("mousemove", onMove);
    elRef.current.addEventListener("click", onClick);

    return () => {
      elRef.current?.removeEventListener("mousemove", onMove);
      elRef.current?.removeEventListener("click", onClick);
    };
  }, [elRef.current]);

  return (
    <svg
      xmlns="http://www.w3.org/2000/svg"
      height={size}
      width={size}
      viewBox="-10 -10 170 170"
      ref={elRef}
      onMouseLeave={reset}
    >
      <defs>
        <mask id={`wptb_star_rating_filter-${id}-${index}`}>
          <rect
            height="150"
            width={Math.max(0, Math.min(value - index, 1)) * 150}
            y="0"
            x="0"
            fill="#fff"
          />
        </mask>
      </defs>

      <path
        fill={"none"}
        strokeWidth="5"
        d="m0.75,56.89914l56.02207,0l17.31126,-56.14914l17.31126,56.14914l56.02206,0l-45.32273,34.70168l17.31215,56.14914l-45.32274,-34.70262l-45.32274,34.70262l17.31215,-56.14914l-45.32274,-34.70168z"
        stroke={displayColor}
      />
      <path
        className="star"
        mask={`url(#wptb_star_rating_filter-${id}-${index})`}
        fill={displayColor}
        strokeWidth="5"
        d="m0.75,56.89914l56.02207,0l17.31126,-56.14914l17.31126,56.14914l56.02206,0l-45.32273,34.70168l17.31215,56.14914l-45.32274,-34.70262l-45.32274,34.70262l17.31215,-56.14914l-45.32274,-34.70168z"
        stroke={displayColor}
      />
    </svg>
  );
};
