import { v4 as uuidv4 } from "uuid";
import { BlockParser } from "@wptb/dev/types";
import { TextBlockProps } from "@wptb/dev/types";
import { hexToRgbA, parseSpacing } from "@wptb/utils/parser";

const parseBlock: BlockParser = (el, allProps, _, tableProps) => {
  let text = "";
  Array.from(el.querySelectorAll("p")).forEach((child) => {
    text += child.outerHTML;
  });

  const id = uuidv4();
  let color: string | undefined = hexToRgbA(el.style.color);
  if (color === tableProps.fontColor) {
    color = undefined;
  }
  let fontSize: string | undefined = el.style.fontSize;
  if (fontSize === tableProps.fontSize) {
    fontSize = undefined;
  }

  allProps[id] = {
    text,
    color,
    fontSize,
    padding: parseSpacing("padding", el.style),
    margin: parseSpacing("margin", el.style),
  } as TextBlockProps;

  return [
    id,
    {
      name: "text",
      blockOrder: [],
    },
  ];
};

export default parseBlock;
