import { v4 as uuidv4 } from "uuid";
import { ListBlockProps, ListItemBlockProps } from "@wptb/dev/types";
import { BlockParser } from "@wptb/dev/types";
import { parseSpacing } from "@wptb/utils/parser";

const parseBlock: BlockParser = (el, allProps, innerBlocks) => {
  const firstLi: HTMLLIElement | null = el.querySelector("li");
  const firstP: HTMLParagraphElement | null | undefined =
    firstLi?.querySelector("p");

  const icon = firstP?.className.match(
    /wptb-list-style-type-(none|circle|square|disc)/
  )?.[1];

  const blockOrder: string[] = [];
  const alignMap = new Map<string, number>();
  const liProps: ListItemBlockProps[] = [];

  const props: ListBlockProps = {
    type: icon ? "unordered" : "ordered",
    listIcon: icon || "disc",
    color: firstP?.style.color,
    fontSize: firstP?.style.fontSize,
    itemSpacing: firstLi?.style.marginBottom,
    padding: parseSpacing("padding", el.style),
    margin: parseSpacing("margin", el.style),
  };

  const listId = uuidv4();
  allProps[listId] = props;

  Array.from(el.querySelectorAll("li")).forEach((child) => {
    const toolTipEl = child.querySelector(".wptb-m-tooltip");
    const tooltipPosision =
      child.className.match(/wptb-tooltip-(top|bottom|left|right)/)?.[1] ||
      "top";
    const pEl = child.querySelector("p");

    const alignment = pEl?.style.textAlign || "left";

    const lastEntry = alignMap.get(alignment);
    if (lastEntry) {
      alignMap.set(alignment, lastEntry + 1);
    } else {
      alignMap.set(alignment, 1);
    }

    const itmProps: ListItemBlockProps = {
      text: pEl?.innerHTML || "",
      alignment,
      toolTip: toolTipEl?.innerHTML || "",
      tooltipPosision,
    };

    const bId = uuidv4();
    blockOrder.push(bId);
    allProps[bId] = itmProps;
    liProps.push(itmProps);

    innerBlocks[bId] = {
      name: "list-item",
      blockOrder: [],
      parentId: listId,
    };
  });

  let alignment: string = "left";
  let aCount = 0;

  alignMap.forEach((value, key) => {
    if (value > aCount) {
      alignment = key;
      aCount = value;
    }
  });

  liProps.forEach((item) => {
    if (item.alignment === alignment) {
      delete item.alignment;
    }
  });

  props.alignment = alignment;

  return [
    listId,
    {
      name: "list",
      blockOrder,
    },
  ];
};

export default parseBlock;
