import { BlockParser } from "@wptb/dev/types";
import { CustomHtmlBlockProps } from "@wptb/dev/types";
import { parseSpacing } from "@wptb/utils/parser";
import { v4 as uuidv4 } from "uuid";

const parseBlock: BlockParser = (el, allBlocks) => {
  const props: CustomHtmlBlockProps = {
    html: el.firstElementChild?.innerHTML || "",
    padding: parseSpacing("padding", el.style),
    margin: parseSpacing("margin", el.style),
  };
  const bid = uuidv4();
  allBlocks[bid] = props;
  return [
    bid,
    {
      name: "custom-html",
      blockOrder: [],
    },
  ];
};

export default parseBlock;
