import { SpacingValue } from "@wptb/dev/types";
import {
  BoxAllSide,
  BoxBottomSide,
  BoxHorizontalSide,
  BoxLeftSide,
  BoxRightSide,
  BoxTopSide,
  BoxVerticalSide,
} from "../../icons/box-sides";
import { SplitIcon, UnSplitIcon } from "../../icons/actions";
import { useState } from "react";
import SizeControl, { AllowedUnits } from "../size-control";
import ControlLabel from "../control-label";

type Side = "top" | "right" | "bottom" | "left" | "vertical" | "horizontal";

const SIDE_ICON_MAP = {
  all: BoxAllSide,
  top: BoxTopSide,
  right: BoxRightSide,
  bottom: BoxBottomSide,
  left: BoxLeftSide,
  vertical: BoxVerticalSide,
  horizontal: BoxHorizontalSide,
} as const;

interface SpacingSingleProps {
  side: Side | "all";
  value?: string;
  onChange: (value: string) => void;
  allowedUnits?: AllowedUnits;
  allowOutOfRange?: boolean;
  allowOutOfRangeMax?: boolean;
  allowOutOfRangeMin?: boolean;
}

const SpacingSingle = ({
  side,
  value,
  onChange,
  allowedUnits,
  allowOutOfRange,
  allowOutOfRangeMax,
  allowOutOfRangeMin,
}: SpacingSingleProps) => {
  return (
    <div className="wptb-spacing-single">
      <div className="wptb-spacing-single-icon">{SIDE_ICON_MAP[side]}</div>
      <SizeControl
        value={value}
        onChange={onChange}
        allowedUnits={allowedUnits}
        allowOutOfRange={allowOutOfRange}
        allowOutOfRangeMax={allowOutOfRangeMax}
        allowOutOfRangeMin={allowOutOfRangeMin}
      />
    </div>
  );
};

interface SpacingControlProps {
  label?: string;
  sides?: Side[];
  value?: SpacingValue;
  onChange?: (value: SpacingValue) => void;
  onReset?: () => void;
  allowedUnits?: AllowedUnits;
  allowOutOfRange?: boolean;
  allowOutOfRangeMax?: boolean;
  allowOutOfRangeMin?: boolean;
}

const defaultAllowedUnits: AllowedUnits = {
  px: {
    min: 0,
    max: 50,
    step: 1,
  },
  em: {
    min: 0,
    max: 5,
    step: 0.2,
  },
  rem: {
    min: 0,
    max: 5,
    step: 0.2,
  },
  "%": {
    min: 0,
    max: 100,
    step: 1,
  },
};

const SpacingControl = ({
  label,
  value,
  onChange,
  onReset,
  sides = ["top", "right", "bottom", "left"],
  allowedUnits = defaultAllowedUnits,
  allowOutOfRange,
  allowOutOfRangeMax,
  allowOutOfRangeMin,
}: SpacingControlProps) => {
  const [isSplit, setIsSplit] = useState(typeof value === "object");

  const changeSplit = () => {
    if (isSplit) {
      onChange?.((value as any).top);
      setIsSplit(false);
    } else {
      onChange?.({
        top: value as any,
        right: value as any,
        bottom: value as any,
        left: value as any,
      });
      setIsSplit(true);
    }
  };

  const setSideValue = (val: string, side: Side) => {
    if (side === "horizontal") {
      onChange?.({
        top: val,
        bottom: val,
        ...(value as any),
      });
    } else if (side === "vertical") {
      onChange?.({
        left: val,
        right: val,
        ...(value as any),
      });
    } else {
      onChange?.({
        ...(value as any),
        [side]: val,
      });
    }
  };

  return (
    <div className="wptb-spacing-control wptb-control">
      <ControlLabel label={label} onReset={onReset}>
        {sides?.length && (
          <button
            type="button"
            onClick={changeSplit}
            className="wptb-split-btn"
          >
            {isSplit ? UnSplitIcon : SplitIcon}
          </button>
        )}
      </ControlLabel>
      {isSplit ? (
        sides.map((side) => {
          let val: any;
          if (side === "horizontal") {
            val = (value as any)?.left;
          } else if (side === "vertical") {
            val = (value as any)?.top;
          } else {
            val = value?.[side];
          }

          return (
            <SpacingSingle
              key={side}
              side={side}
              value={val}
              allowedUnits={allowedUnits}
              onChange={(val) => setSideValue(val, side)}
              allowOutOfRange={allowOutOfRange}
              allowOutOfRangeMax={allowOutOfRangeMax}
              allowOutOfRangeMin={allowOutOfRangeMin}
            />
          );
        })
      ) : (
        <SpacingSingle
          side="all"
          value={value as string}
          onChange={(val) => onChange?.(val)}
          allowedUnits={allowedUnits}
          allowOutOfRange={allowOutOfRange}
          allowOutOfRangeMax={allowOutOfRangeMax}
          allowOutOfRangeMin={allowOutOfRangeMin}
        />
      )}
    </div>
  );
};

export default SpacingControl;
