import { BlockControlProps } from "@wptb/dev/types";
import { TextBlockProps } from "@wptb/dev/types";
import {
  ColorControl,
  PanelBody,
  SizeControl,
  SpacingControl,
} from "@wptb/ui/components";
import UpsellControl from "@/builder/components/upsell-control";

const Control = ({
  blockProps,
  setBlockProps,
}: BlockControlProps<TextBlockProps>) => {
  return (
    <PanelBody title="TEXT OPTIONS" className="wptb-settings" initialOpen>
      <ColorControl
        label="Font Color"
        value={blockProps?.color}
        onReset={() => setBlockProps({ color: undefined })}
        onChange={(color) => setBlockProps({ color })}
      />
      <ColorControl
        label="Link Font Color"
        value={blockProps?.linkColor}
        onReset={() => setBlockProps({ linkColor: undefined })}
        onChange={(linkColor) => setBlockProps({ linkColor })}
      />
      <SizeControl
        label="Font Size"
        value={blockProps?.fontSize}
        onReset={() => setBlockProps({ fontSize: undefined })}
        onChange={(fontSize) => setBlockProps({ fontSize })}
        allowOutOfRangeMax
      />
      <UpsellControl name="text.padding">
        <SpacingControl label="Padding" value={blockProps?.padding} />
      </UpsellControl>
      <UpsellControl name="text.margin">
        <SpacingControl label="Margin" value={blockProps?.margin} />
      </UpsellControl>
    </PanelBody>
  );
};
export default Control;
