import UpsellControl from "@/builder/components/upsell-control";
import {
  AlignmentControl,
  ColorControl,
  PanelBody,
  SelectControl,
  SizeControl,
  SpacingControl,
  Tab,
} from "@wptb/ui/components";
import { BlockControlProps } from "@wptb/dev/types";
import { ListBlockProps } from "@wptb/dev/types";
import { useState } from "react";

interface TabAttrs {
  blockProps: ListBlockProps;
  setBlockProps: (props: Partial<ListBlockProps>) => void;
}

const GeneralTab = ({ blockProps, setBlockProps }: TabAttrs) => {
  return (
    <div className="wptb-control-group">
      <SelectControl
        label="List Type"
        options={[
          {
            value: "unordered",
            label: "Unordered",
          },
          {
            value: "ordered",
            label: "Ordered",
          },
        ]}
        value={blockProps.type}
        onChange={(type: any) => setBlockProps({ type })}
      />
      {blockProps.type === "unordered" ? (
        <SelectControl
          label="List Icon"
          options={[
            {
              value: "circle",
              label: "Circle",
            },
            {
              value: "square",
              label: "Square",
            },
            {
              value: "disc",
              label: "Disc",
            },
            {
              value: "none",
              label: "None",
            },
          ]}
          value={blockProps.listIcon}
          onChange={(listIcon: any) => setBlockProps({ listIcon })}
        />
      ) : null}
      <UpsellControl name="list.padding">
        <SpacingControl label="Padding" value={blockProps?.padding} />
      </UpsellControl>
      <UpsellControl name="list.margin">
        <SpacingControl label="Margin" value={blockProps?.margin} />
      </UpsellControl>
    </div>
  );
};

const FontTab = ({ blockProps, setBlockProps }: TabAttrs) => {
  return (
    <div className="wptb-control-group">
      <ColorControl
        label="Text Color"
        value={blockProps?.color}
        onReset={() => setBlockProps({ color: undefined })}
        onChange={(color) => setBlockProps({ color })}
      />
      <SizeControl
        label="Font Size"
        value={blockProps?.fontSize}
        onReset={() => setBlockProps({ fontSize: undefined })}
        onChange={(fontSize) => setBlockProps({ fontSize })}
      />
    </div>
  );
};

const LayoutTab = ({ blockProps, setBlockProps }: TabAttrs) => {
  return (
    <div className="wptb-control-group">
      <SizeControl
        label="Item Spacing"
        value={blockProps?.itemSpacing}
        onReset={() => setBlockProps({ itemSpacing: undefined })}
        onChange={(itemSpacing) => setBlockProps({ itemSpacing })}
      />
      <AlignmentControl
        label="List Alignment"
        value={blockProps?.alignment}
        onChange={(alignment) => setBlockProps({ alignment })}
      />
    </div>
  );
};

const Control = ({
  blockProps,
  setBlockProps,
}: BlockControlProps<ListBlockProps>) => {
  const [activeTab, setActiveTab] = useState<string>("general");
  return (
    <PanelBody title="List" initialOpen className="wptb-settings">
      <Tab
        active={activeTab}
        setActive={setActiveTab}
        tabs={{
          general: { title: "General", component: GeneralTab },
          font: { title: "Font", component: FontTab },
          layout: { title: "Layout", component: LayoutTab },
        }}
        props={{ blockProps, setBlockProps }}
      />
    </PanelBody>
  );
};

export default Control;
